@import "../../sass/_vars.scss";

.header {
  position: fixed;
  width: 100%;
  background: $black-secondary;
  color: $white;
  font-size: 16px;
}

.container__header {
  margin: 0 30px;
  @media (max-width: 440px) {
    margin: 0 10px;
  }
}

.menu {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 6px 0;
  @media (max-width: 400px) {
    justify-content: space-evenly;
    font-size: 14px;
  }
}

.menu__title {
  margin-right: 15px;
}

.menu__requestTime {
  margin-right: 15px;
}

.menu__buttonUpdate {
  background: none;
  border: none;
  color: $white;
  padding: 0;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.2s linear;
  &:hover {
    text-decoration: underline;
    color: $blue;
  }
  @media (max-width: 400px) {
    font-size: 14px;
  }
}
