@import "../../sass/_vars.scss";

.conversion {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 50px;
  background: $black-primary;
}

.conversion__string {
  margin-bottom: 20px;
  @media (max-width: 700px) {
    & {
      display: flex;
      flex-direction: column;
      align-items: center;
      justify-content: center;
    }
  }
}

.conversion__title {
  font-size: 34px;
  margin: 40px 0 20px;
  color: $white;
  @media (max-width: 700px) {
    & {
      font-size: 26px;
      margin-top: 30px;
    }
  }
}
