@import "../../sass/_vars.scss";

.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: $black-primary;
}

.table {
  margin: 20px 30px;
  @media (max-width: 500px) {
    & {
      margin: 20px 0px;
      width: 90%;
    }
  }
}

.table__content {
  @media (max-width: 500px) {
    & {
      display: flex;
      overflow-x: auto;
    }
  }
}

.table__inner {
  box-shadow: 9px 9px 10px $black-secondary;
}

.table__text-error {
  color: $white;
  font-size: 16px;
  @media (max-width: 700px) {
    & {
      font-size: 14px;
    }
  }
}
