.header {
  position: fixed;
  width: 100%;
  background: #131212;
  color: white;
  font-size: 16px;
}

.container__header {
  margin: 0 30px;
}
@media (max-width: 440px) {
  .container__header {
    margin: 0 10px;
  }
}

.menu {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 6px 0;
}
@media (max-width: 400px) {
  .menu {
    justify-content: space-evenly;
    font-size: 14px;
  }
}

.menu__title {
  margin-right: 15px;
}

.menu__requestTime {
  margin-right: 15px;
}

.menu__buttonUpdate {
  background: none;
  border: none;
  color: white;
  padding: 0;
  font-size: 16px;
  cursor: pointer;
  transition: all 0.2s linear;
}
.menu__buttonUpdate:hover {
  text-decoration: underline;
  color: #47d1bc;
}
@media (max-width: 400px) {
  .menu__buttonUpdate {
    font-size: 14px;
  }
}
.input {
  outline: none;
  padding: 6px 10px;
  border-radius: 6px;
  border: 1px solid #47d1bc;
  background: #131212;
  color: #47d1bc;
  text-align: center;
  font-size: 16px;
  box-shadow: 9px 9px 10px #131212;
}
.input--conversion {
  margin-right: 30px;
}
.input--search {
  margin-top: 50px;
  width: 100%;
  max-width: 458px;
}
@media (max-width: 800px) {
  .input--search {
    margin-top: 60px;
    width: 85%;
  }
}
@media (max-width: 650px) {
  .input--search {
    max-width: 458px;
  }
}
@media (max-width: 600px) {
  .input--search {
    max-width: 420px;
  }
}
@media (max-width: 500px) {
  .input--search {
    width: 85%;
    max-width: 450px;
  }
}
.input::-webkit-input-placeholder {
  color: #47d1bc;
}
.input:-ms-input-placeholder {
  color: #47d1bc;
}
.input::placeholder {
  color: #47d1bc;
}
@media (max-width: 700px) {
  .input--conversion {
    padding: 6px;
    margin-right: 0;
    width: 319px;
    margin-bottom: 10px;
    font-size: 14px;
  }
  .input::-webkit-input-placeholder {
    font-size: 14px;
  }
  .input:-ms-input-placeholder {
    font-size: 14px;
  }
  .input::placeholder {
    font-size: 14px;
  }
}
@media (max-width: 600px) {
  .input--conversion {
    width: 277px;
  }
  .input--conversion::-webkit-input-placeholder {
    font-size: 12px;
  }
  .input--conversion:-ms-input-placeholder {
    font-size: 12px;
  }
  .input--conversion::placeholder {
    font-size: 12px;
  }
}
@media (max-width: 500px) {
  .input--conversion {
    width: 237px;
  }
}
.select {
  outline: none;
  padding: 6px 10px;
  border-radius: 6px;
  border: 1px solid #47d1bc;
  background: #131212;
  color: #47d1bc;
  text-align: center;
  font-size: 16px;
  box-shadow: 9px 9px 10px #131212;
}
@media (max-width: 700px) {
  .select {
    font-size: 14px;
  }
}
@media (max-width: 600px) {
  .select {
    font-size: 12px;
  }
}
@media (max-width: 500px) {
  .select {
    font-size: 10px;
  }
}
.conversion {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin-bottom: 50px;
  background: #1b1919;
}

.conversion__string {
  margin-bottom: 20px;
}
@media (max-width: 700px) {
  .conversion__string {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
}

.conversion__title {
  font-size: 34px;
  margin: 40px 0 20px;
  color: white;
}
@media (max-width: 700px) {
  .conversion__title {
    font-size: 26px;
    margin-top: 30px;
  }
}
td {
  color: #47d1bc;
  border-collapse: collapse;
  border-spacing: 0;
  border: 1px solid #47d1bc;
  padding: 10px;
  text-align: center;
  background: #131212;
}
@media (max-width: 600px) {
  td {
    padding: 7px;
  }
}
th {
  color: #47d1bc;
  border-collapse: collapse;
  border-spacing: 0;
  border: 1px solid #47d1bc;
  padding: 10px;
  text-align: center;
  background: #131212;
}
@media (max-width: 600px) {
  th {
    padding: 7px;
  }
}
.container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background: #1b1919;
}

.table {
  margin: 20px 30px;
}
@media (max-width: 500px) {
  .table {
    margin: 20px 0px;
    width: 90%;
  }
}

@media (max-width: 500px) {
  .table__content {
    display: flex;
    overflow-x: auto;
  }
}

.table__inner {
  box-shadow: 9px 9px 10px #131212;
}

.table__text-error {
  color: white;
  font-size: 16px;
}
@media (max-width: 700px) {
  .table__text-error {
    font-size: 14px;
  }
}
.my-button {
  padding: 6px 10px;
  border-radius: 6px;
  border: 1px solid white;
  text-align: center;
  color: white;
  font-size: 16px;
  background: #131212;
  cursor: pointer;
  box-shadow: 9px 9px 10px #131212;
  transition: all 0.2s linear;
}
.my-button:hover {
  border-color: #47d1bc;
  color: #47d1bc;
  background: #131212;
}
@media (max-width: 560px) {
  .my-button {
    font-size: 14px;
  }
}
.center {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}

.loader {
  display: inline-block;
  position: relative;
  width: 80px;
  height: 80px;
}
.loader div {
  display: inline-block;
  position: absolute;
  left: 8px;
  width: 16px;
  background: #47d1bc;
  -webkit-animation: loader 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
          animation: loader 1.2s cubic-bezier(0, 0.5, 0.5, 1) infinite;
}
.loader div:nth-child(1) {
  left: 8px;
  -webkit-animation-delay: -0.24s;
          animation-delay: -0.24s;
}
.loader div:nth-child(2) {
  left: 32px;
  -webkit-animation-delay: -0.12s;
          animation-delay: -0.12s;
}
.loader div:nth-child(3) {
  left: 56px;
  -webkit-animation-delay: 0;
          animation-delay: 0;
}

@-webkit-keyframes loader {
  0% {
    top: 8px;
    height: 64px;
  }
  50%, 100% {
    top: 24px;
    height: 32px;
  }
}

@keyframes loader {
  0% {
    top: 8px;
    height: 64px;
  }
  50%, 100% {
    top: 24px;
    height: 32px;
  }
}
.app {
  min-height: 100vh;
  font-family: "Roboto", "-apple-system", "BlinkMacSystemFont", "Helvetica Neue", Helvetica, sans-serif;
  overflow: hidden;
  background-color: #1b1919;
}

.text-error {
  margin: 50px 60px;
  font-size: 24px;
  line-height: 1.2;
  color: white;
  text-align: center;
}
.text-error a {
  color: white;
  text-decoration: none;
}
@media (max-width: 700px) {
  .text-error {
    line-height: 1.4;
    margin: 50px 30px;
    font-size: 18px;
  }
}
@media (max-width: 500px) {
  .text-error {
    margin: 50px 15px;
    font-size: 14px;
  }
}
